import React, { useEffect, useState } from 'react'
import "./menupage.css"
import { Badge } from '@mui/material';
import { styled } from '@mui/material/styles';
import IconButton from '@mui/material/IconButton';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import axios from 'axios'

const StyledBadge = styled(Badge)(({ theme }) => ({
  '& .MuiBadge-badge': {
    right: -3,
    top: 13,
    border: `2px solid ${theme.palette.background.paper}`,
    padding: '0 4px',
  },
}));


const MenuPage = () => {
const[search,setSearch]=useState()
  const[data,setData]=useState([])
  useEffect(()=>{
    axios.get("https://api.escuelajs.co/api/v1/products").then(
      res=>setData(res.data)
    )

  },[])
  return (
    <div className='back'><nav className="navbar navbar-expand-lg navbar-light bg-light">
    <div className="container-fluid">
      <a className="navbar-brand" href="#">Hii</a>
      
      <div className="collapse navbar-collapse" id="navbarSupportedContent"> 
      <form className="d-flex">
          <input className="form-control" type="search" placeholder="Search for Items" aria-label="Search"
          value={search} onChange={(e)=>setSearch(e.target.value)}
          />
        </form>
        <ul className="navbar-nav me-auto mb-2 mb-lg-0">
        <select className="form-select" aria-label="Default select example">
  <option selected> All</option>
  <option value="1">Break Fast</option>
  <option value="2">Lunch</option>
  <option value="3">snacks</option>
  <option value="3">Dinner</option>
</select>
</ul>
<IconButton aria-label="cart">
     
<StyledBadge badgeContent={1} color="secondary">
        <ShoppingCartIcon />
      </StyledBadge>
    </IconButton>

      
</div>
    </div>
  </nav>
  <div className="col-lg-12 col-md-4 body">
    <div className='row main' >
{data.map((product) => (

    <div className="card" key={product.id}>

        <img src={product.images} alt=""  className='Image'/>

        <h4 className='title'>{product.title}</h4>
        <h5 className='price'>Price:${product.price}</h5>
        {/* <h6 className='discription'>{product.description}</h6> */}
        <div class="btn-group" role="group" aria-label="Basic mixed styles example">
 
  <button type="button" class="btn btn-primary">Add to cart</button> &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
  <button type="button" class="btn btn-success">Oreder Now</button>
</div>
    </div>

))}

</div>
</div>
  </div>
  )
}

export default MenuPage